            </div>
            <div style="display: flex;justify-content: center;padding-top: 1.233rem;">
                <input type="text" placeholder="è¾“å…¥é‚®ç®±åœ°å€" ref="sendmail">
            </div>
            <div class="tip_sure" @click="sureSend">ç¡®å®š</div>
        </div>

        <div style="width: 100% ;background-color: white;position:fixed;bottom: 0" v-if="show9">
            <div class="sendEmail" @click="sendmessage">
                å‘é€è®¿å®¢è®°å½•åˆ°é‚®ç®±
            </div>
        </div>
    </div>


</template>

<script type="text/javascript">

    import qs from 'qs';
    import {formatDate} from '@/assets/js/util/formatdate.js'
    import refresh from './refresh'
    import {Toast} from 'mint-ui';
    import {Indicator} from 'mint-ui';
    import {MessageBox} from 'mint-ui';
    import Vue from 'vue'
    import VueScroller from 'vue-scroller'

    Vue.use(VueScroller);
    export default {
        filters: {
            formatDate(time) {
                var date = new Date(time);
                return formatDate(date)
            }
        },
        name: 'Inquire',
        props: {date: String},  //æŽ¥å—çˆ¶ç»„ä»¶ä¸­è¯·æ±‚jsonå¾—åˆ°çš„æ—¥æœŸå€¼
        data() {
            return {
                dateVal: new Date(), // é»˜è®¤æ˜¯å½“å‰æ—¥æœŸ
                dateVal1: new Date(),
                selectedValue: '',
                selectedValue1: '',
                buildingId: localStorage.getItem('WbuildingId'),
                mobilePhoneNo: localStorage.getItem('mobilePhoneNo'),
                list: {},
                top: 0,
                startY: 0,          // ä¿å­˜ yè½´ç‚¹çš„ä½ç½®
                touching: false,   // ä»£è¡¨å½“å‰æ˜¯å¦å¤„äºŽ ä¸‹æ‹‰åˆ·æ–°è¡Œä¸ºçš„å¼€å…³ï¼Œä¹Ÿå°±æ˜¯å½“å±žäºŽæ»šåŠ¨è¡Œä¸ºæ—¶ï¼Œå°±è¦é€€å‡ºè¯¥äº‹ä»¶æœºåˆ¶
                num: 0,
                keywords: '',
                show5: false,
                visitorRecordList: [],
                startDate1: new Date(),
                endDate: new Date(),
                endDate1: new Date(),
                show7: false,
                show8: false,
                show9: true,//æŽ§åˆ¶å‘é€é‚®ä»¶æ˜¾å½±
            }
        },
        // created() {
        //     this.selectedValue1 = this.selectedValue = formatDate(new Date())
        //     this.getData(this.selectedValue, this.selectedValue1, this.keywords,0);
        // },
        /**åˆå§‹åŒ–æ–¹æ³•*/
        activated() {
            if (!this.$route.meta.isBack) {
                this.visitorRecordList = [];
                this.num = 0;
                this.selectedValue1 = this.selectedValue = formatDate(new Date());
                this.getData(this.selectedValue, this.selectedValue1, this.keywords,0);
            }
            this.$route.meta.isBack = false;
        },
        //åˆ¤æ–­ä»Žå“ªé‡Œè·³è½¬è¿‡æ¥çš„ï¼Œæ˜¯å¦ä¿å­˜é¡µé¢ç¼“å­˜
        beforeRouteEnter(to, from, next) {
            if (from.path == "/detail") {
                to.meta.isBack = true;
            } else {
                to.meta.isBack = false;
            }
            next();
        },
        methods: {
            //å–æ¶ˆå‘é€
            quxiao() {
                this.show7 = false;
                this.show8 = false;//å–æ¶ˆå‘é€ï¼Œè’™ç‰ˆæ¶ˆå¤±
            },
            //ç¡®è®¤å‘é€é‚®ä»¶
            sureSend() {
                this.show7 = false;
                this.show8 = false;//ç¡®è®¤å‘é€ï¼Œè’™ç‰ˆæ¶ˆå¤±
                let msg = this.$refs.sendmail.value;
                let reg = /^[_a-z0-9-]+(\.[_a-z0-9-]+)*@[a-z0-9-]+(\.[a-z0-9-]+)*(\.[a-z]{2,})$/;
                if (!reg.test(msg) && msg.length != 0) {
                    MessageBox('æç¤º', 'è¯·è¾“å…¥æ­£ç¡®çš„é‚®ç®±');
                    return false;
                } else if (msg == "") {
                    MessageBox('æç¤º', 'è¯·è¾“å…¥é‚®ç®±');
                    return false;
                } else {
                    Indicator.open('é‚®ä»¶å‘é€ä¸­...');
                    this.sendE(msg, this.selectedValue, this.selectedValue1, this.keywords)
                }

            },
            //ç‚¹å‡»å‘é€é‚®ä»¶
            sendmessage() {
                this.show7 = true;
                this.show8 = true;//ç‚¹å‡»å‘é€ è’™ç‰ˆå’Œæ¨¡å—æ˜¾ç¤º
            },
            into(i) {
                this.$store.state.msg = i;
                this.$router.push({path: "/detail"})
            },
            //ç‚¹å‡»é”®ç›˜æœç´¢å‡ºå‘æœç´¢åŠŸèƒ½
            searchBtn(event) {
                // console.log(event)
                if (event.keyCode === 13) { //å¦‚æžœæŒ‰çš„æ˜¯enteré”® 13æ˜¯enter
                    event.preventDefault(); //ç¦æ­¢é»˜è®¤äº‹ä»¶ï¼ˆé»˜è®¤æ˜¯æ¢è¡Œï¼‰s
                    this.keywords = event.target.value;
                    var search_val = event.target.value;
                    if (search_val === '' || search_val === null) {
                        this.getData(this.selectedValue, this.selectedValue1, this.keywords,0);
                    } else {
                        this.getData(this.selectedValue, this.selectedValue1, this.keywords,0);
                    }
                }

            },
            getData(a, b, c,pageno) {
                let bid = localStorage.getItem('WbuildingId');
                let phone = localStorage.getItem('mobilePhoneNo');
                let token = localStorage.getItem('WToken');
                if(pageno == 0){
                    this.num = 0;
                    this.visitorRecordList=[];
                }else{
                    if(this.show5)
                        return;
                }
                const data = {
                    buildingId: bid,
                    mobilePhoneNo: phone,
                    "queryStartDate": a,
                    "queryEndDate": b,
                    "queryKeyword": c,
                    "pageNo": pageno,
                    "pageSize": 7
                };
                const options = {
                    method: 'POST',
                    headers: {'content-type': 'application/json', 'token': token},
                    data: qs.parse(data),
                    url: '/visit/building/queryVisitorRecord'
                };
                this.axios(options).then((response) => {
                    this.list = response.data.data;
                    let datalist = this.list.visitorRecordList;
                    if (datalist != null) {
                        for(let i in datalist){
                            this.visitorRecordList.push(datalist[i]);
                        }
                        this.num ++;
                        this.show5 = datalist.length < 7;
                        this.$refs.more.innerHTML = datalist.length < 7 ? 'æš‚æ— æ›´å¤š' : 'ç‚¹å‡»åŠ è½½æ›´å¤š...';
                    }else{
                        this.$refs.more.innerHTML = 'æš‚æ— æ›´å¤š';
                        this.show5 = true;
                    }
                })
            },

            selectData() { // æ‰“å¼€æ—¶é—´é€‰æ‹©å™¨
                this.endDate = this.dateVal1;
                this.$refs['datePicker'].open();
                this.show9 = false;
            },
            selectData1() { // æ‰“å¼€æ—¶é—´é€‰æ‹©å™¨
                this.startDate1 = this.dateVal;
                this.$refs['datePicker1'].open();
                this.show9 = false;
            },
            dateConfirm() { // æ—¶é—´é€‰æ‹©å™¨ç¡®å®šæŒ‰é’®ï¼Œå¹¶æŠŠæ—¶é—´è½¬æ¢æˆæˆ‘ä»¬éœ€è¦çš„æ—¶é—´æ ¼å¼
                this.selectedValue = formatDate(this.dateVal);
                this.getData(this.selectedValue, this.selectedValue1, this.keywords,0);
                this.$refs.first.style.color = "#333333";
                this.show9 = true;
            },
            dateConfirm1() { // æ—¶é—´é€‰æ‹©å™¨ç¡®å®šæŒ‰é’®ï¼Œå¹¶æŠŠæ—¶é—´è½¬æ¢æˆæˆ‘ä»¬éœ€è¦çš„æ—¶é—´æ ¼å¼
                this.selectedValue1 = formatDate(this.dateVal1);
                this.getData(this.selectedValue, this.selectedValue1, this.keywords,0);
                this.$refs.second.style.color = "#333333";
                this.show9 = true;
            },
            datacancel() {
                this.show9 = true;
            },
            datacancel1() {
                this.show9 = true;
            }
            ,
            //å‘é€é‚®ä»¶
            sendE(msg, a, b, c) {
                console.log(this.buildingId);
                const data = {
                    "buildingId": this.buildingId,
                    "mobilePhoneNo": this.mobilePhoneNo,
                    "email": msg,
                    "queryStartDate": a,
                    "queryEndDate": b,
                    "queryKeyword": c,
                };
                const options = {
                    method: 'POST',
                    headers: {'content-type': 'application/json', 'token': localStorage.getItem('WToken')},
                    data: data,
                    url: '/visit/building/sendVisitorRecordMail'
                };
                this.axios(options).then((res) => {
                    // console.log(res.data)

                    if (res.data.state == 1) {
                        Indicator.close();//é‚®ä»¶å‘é€å®Œæ¯•
                        Toast({
                            message: 'å‘é€æˆåŠŸ',
                            duration: 500
                        })
                    } else {
                        Indicator.close();
                        Toast({
                            message: res.data.retMsg,
                            duration: 1500
                        })
                    }
                })
            }
        }
    }
</script>

<style lang="scss" scoped>
    //é‚®ç®±æç¤ºæ¡†
    .tip {
