    }

    .table {
        border-top: #ddd solid .5px;

        li {
            border-bottom: #ddd solid .5px;
            height: 2.553rem;
            padding-left: .638rem;

            a {
                height: 2.553rem;
                display: block;
                overflow: hidden;

                label {
                    float: left;
                }

                img {
                    width: 1.702rem;
                    height: 1.702rem;
                    margin: .426rem .638rem 0 0;

                }

                .media {
                    float: left;
                    margin-top: .34rem;

                    h4 {
                        height: 1.064rem;
