    }

    .registration {

        .showTime {
            height: 2.128rem;
            /*border-bottom: #ddd solid .5px;*/
            line-height: 2.128rem;
            font-size: .638rem;
            color: #999;
            padding-left: .638rem;

            input {
                border: none;
                border-bottom: #ddd solid .5px;
                width: 100%;
                height: 2.128rem;
