<template>
  <div
    class="weui-pull-refresh"
    ref="scrollBox"
    :style="style"
    @touchstart="touchStart"
    @touchmove="touchMove"
    @touchend="touchEnd">
    <div class="weui-pull-refreshing-box">
      <div v-if="moveState < 2">{{ moveState === 0 ? 'ä¸‹æ‹‰å³å¯åˆ·æ–°...' : 'é‡Šæ”¾å³å¯åˆ·æ–°...' }}</div>
      <div v-else><i class="weui-loading"/> åŠ è½½ä¸­...</div>
    </div>
    <div class="weui-pull-present-box">
      <slot/>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'pull-refresh',
    data() {
      return {
        startY: '',    //ä¿å­˜touchæ—¶çš„Yåæ ‡
        moveDistance: 0,    //ä¿å­˜å‘ä¸‹æ»‘åŠ¨çš„è·ç¦»
        moveState: 0,        //å¼€å§‹æ»‘åŠ¨åˆ°ç»“æŸåŽçŠ¶æ€çš„å˜åŒ– 0:ä¸‹æ‹‰å³å¯åˆ·æ–° 1:é‡Šæ”¾å³å¯åˆ·æ–° 2:åŠ è½½ä¸­
        duration: 0        //åŠ¨ç”»æŒç»­æ—¶é—´ï¼Œ0å°±æ˜¯æ²¡æœ‰åŠ¨ç”»
      }
    },
    computed: {
      style() {
        return {
          transition: `${this.duration}ms`,
          transform: `translate3d(0,${this.moveDistance}px, 0)`
        }
      }
    },
    methods: {
      touchStart(e) {
        this.duration = 0 // å…³é—­åŠ¨ç”»
        this.moveDistance = 0 // æ»‘åŠ¨è·ç¦»å½’0
        this.startY = e.targetTouches[0].clientY  // èŽ·å¾—å¼€å§‹Yåæ ‡
      },
      touchMove(e) {    //è¿™é‡Œæ˜¯æ•´ä¸ªä¸‹æ‹‰åˆ·æ–°çš„æ ¸å¿ƒ
        let scrollTop = document.documentElement.scrollTop || document.body.scrollTop
        //é¦–å…ˆåˆ¤æ–­æˆ‘ä»¬æœ‰æ²¡æœ‰æ»šåŠ¨æ¡ï¼Œå¦‚æžœæœ‰ï¼Œæˆ‘ä»¬ä¸‹æ‹‰åˆ·æ–°å°±ä¸èƒ½å¯ç”¨ã€‚
        if (scrollTop > 0) return

        let move = -e.targetTouches[0].clientY + this.startY
        //åˆ¤æ–­æ‰‹æŒ‡æ»‘åŠ¨çš„è·ç¦»ï¼Œåªæœ‰ä¸ºæ­£æ•°æ‰ä»£è¡¨ç”¨æˆ·ä¸‹æ‹‰äº†ã€‚
        if (move > 0) {
          //é˜»æ­¢é»˜è®¤äº‹ä»¶ï¼Œåœ¨å¾®ä¿¡æµè§ˆå™¨ä¸­å°¤ä¸ºæœ‰ç”¨ï¼Œè‡³äºŽä¸ºä»€ä¹ˆï¼Œä½ åŽ»è¯•å°±çŸ¥é“äº†ã€‚
          e.preventDefault()
          //å¢žåŠ æ»‘åŠ¨é˜»åŠ›çš„æ„Ÿè§‰
          this.moveDistance = Math.pow(move, 0.8)
          if (this.moveDistance > 50) {
            //å¦‚æžœæ»‘åŠ¨è·ç¦»å¤§äºŽ50 é‚£æˆ‘å°±å‘Šè¯‰ä½ ï¼Œé‡Šæ”¾å³å¯åˆ·æ–°
            if (this.moveState === 1) return
            this.moveState = 1
          } else {
            //å¦åˆ™ æ¢å¤åŽŸæ ·
            if (this.moveState === 0) return
            this.moveState = 0
          }
        }
      },
      touchEnd(e) {
        // åªè¦æ‰‹æŒ‡æ‹¿å¼€ï¼Œæˆ‘éƒ½éœ€è¦åŠ ä¸Šç»“æŸæ—¶çš„åŠ¨ç”»ï¼Œè¿™é‡Œä¸º300ms
        this.duration = 300
        if (this.moveDistance > 50) {
          //è¿™é‡Œé€»è¾‘è·ŸtouchMoveä¸€æ ·ï¼Œä½†æ˜¯éœ€è¦çœŸçš„åŠ è½½æ•°æ®äº†ï¼Œé‚£moveStateå˜ä¸º2 æ‰€ä»¥åŠ è½½åŠ¨ç”»åœ¨è¿™å‡ºçŽ°
          this.moveState = 2
          //å› ä¸ºè¿˜æ²¡åŠ è½½å®Œï¼Œæˆ‘å¾—è®©åŠ è½½åŠ¨ç”»æ˜¾ç¤ºç€ï¼Œæ‰€ä»¥è¿™é‡Œç§»åŠ¨è·ç¦»ä¸º50
          this.moveDistance = 50
          this.$emit('refresh', () => {
            //è¿™é‡Œå°±æ˜¯æˆåŠŸåŽçš„å›žè°ƒäº†ï¼Œå¦‚æžœè¯¥å‡½æ•°è¢«è°ƒç”¨ï¼Œé‚£å°±ä»¥ä¸ºç€åŠ è½½æ•°æ®å®Œæˆï¼Œæ‰€ä»¥çŠ¶æ€è¦å›žåˆ°0ï¼Œå½“ç„¶éœ€è¦åœ¨çˆ¶ç»„ä»¶è°ƒç”¨ã€‚
            this.moveState = 0
          })
        } else {
          //å¦åˆ™ ç»™æˆ‘è€è€å®žå®žæ¢å¤åŽŸæ ·
          this.moveDistance = 0
        }
      }
    },
    watch: {
      //è¿™é‡Œæ˜¯ç»™ç”¨æˆ·æ“ä½œè¿”å›žçš„æ ¸å¿ƒ
      moveState(state) {
        //æˆ‘ä»¬ç›‘å¬moveStateçš„çŠ¶æ€ï¼Œ
        //0æ„å‘³ç€å¼€å§‹ä¹Ÿæ„å‘³ç€ç»“æŸï¼Œè¿™é‡Œæ˜¯ç»“æŸï¼Œå¹¶ä¸”åªæœ‰åŠ¨ç”»ç”Ÿæ•ˆæˆ‘ä»¬æ‰èƒ½ moveDistance è®¾ä¸º0ï¼Œ
        //ä¸ºä»€ä¹ˆåŠ¨ç”»ç”Ÿæ•ˆæ‰è¡Œï¼Œå› ä¸ºåŠ¨ç”»ç”Ÿæ•ˆæ„å‘³ç€æ‰‹æŒ‡ç¦»å¼€äº†å±å¹•ï¼Œå¦‚æžœä¸æ‡‚åŽ»çœ‹touchEndæ–¹æ³•ï¼Œè¿™æ—¶
        //æˆ‘ä»¬è®©è·ç¦»å˜ä¸º0æ‰ä¼šæœ‰åŠ¨ç”»æ•ˆæžœã€‚
        if (state === 0 && this.duration === 300) {
          this.moveDistance = 0
        }
      }
    }
  }
</script>
<!-- cssæˆ‘å°±ä¸è®²äº†ï¼Œä½ è‚¯å®šæ¯”æˆ‘åŽ‰å®³ -->
<style scoped lang="scss">
  .weui-pull-refresh {
    display: flex;
    height: calc(100vh - 50px);
    flex-direction: column;
    margin-top: -2.128rem;
    .weui-pull-refreshing-box {
      height: 1.191rem;
      font-size: 14px;
      color: rgba(69, 90, 100, .6);
      text-align: center;
      margin-bottom: .851rem;
    }
    .weui-pull-present-box {
      background-color: lighten(#fff, 10%);
      position: relative;
      height: 0;
    }
  }
</style>




