                    if (code == 1010) {
                        var value = a.value;
                        var reg = /^[äº¬æ´¥æ²ªæ¸å†€è±«äº‘è¾½é»‘æ¹˜çš–é²æ–°è‹æµ™èµ£é„‚æ¡‚ç”˜æ™‹è’™é™•å‰é—½è´µç²¤é’è—å·å®ç¼ä½¿é¢†A-Z]{1}[A-Z]{1}[A-Z0-9]{4}[A-Z0-9æŒ‚å­¦è­¦æ¸¯æ¾³]{1}$/;
                        if (!reg.test(value) && value.length != 0) {
                            MessageBox('æç¤º', 'è¯·è¾“å…¥æ­£ç¡®çš„è½¦ç‰Œå·');
                            this.isDisable = false;
                            return false;
                        } else if (value == "") {
                            MessageBox('æç¤º', 'è¯·è¾“å…¥è½¦ç‰Œå·');
                            this.isDisable = false;
                            return false;
                        }
                    }
                }
                //æ‹¿åˆ°é¡µé¢æ•°æ®åšæˆjsonæ ¼å¼
                var array = [];
                if (list1.length > 0) {
                    for (var i = 0; i < list1.length; i++) {
                        var a = list1[i];
                        console.log(a);
                        var code = '' + a.getAttribute("data-code");
                        var is_required = '' + a.getAttribute("data-is-required");
                        var value = '';
                        if (code == '1004') {
                            var c = document.getElementsByClassName('psshow')[0].querySelector('.active');
                            value = c.getAttribute("data-flag");
                        } else if (code == '1003') {
                            value = this.state2
                        } else {
                            value = a.value;
                        }
                        var object = {};
                        object.code = code;
                        object.is_required = is_required;
                        object.value = value;
                        array.push(object);
                    }
                }
                var list2 = document.querySelectorAll("input[data-is-required='0']");
                if (list1.length > 0) {
                    for (var i = 0; i < list2.length; i++) {
                        var a = list2[i];
                        var code = '' + a.getAttribute("data-code");
                        console.log(code);
                        var is_required = '' + a.getAttribute("data-is-required");
                        var value = a.value;
                        var object = {};
                        object.code = code;
                        object.is_required = is_required;
                        object.value = value;
                        array.push(object);
                    }
                }
                console.log(object, list1);
                let visitorRegisterInfo = JSON.stringify(array);
                let openId = localStorage.getItem('openId');
                //å‘ç”Ÿè¯·æ±‚
                // console.log(openId);
                const data = {
                    "buildingId": this.buildingId,
                    "registerTime": this.registerTime,
                    "visitorRegisterInfo": visitorRegisterInfo,
                    "openId": openId,
                    "companyId": this.comId
                };
                // console.log(this.comId);
                const options = {
                    method: 'POST',
                    headers: {'content-type': 'application/json'},
                    data: qs.parse(data),
                    url: 'visit/visitor/registerSubmit'
                };
                this.axios(options).then((response) => {
                    // console.log(response.data);
                    this.isDisable = false;//é‡Šæ”¾æŒ‰é’®çš„ç‚¹å‡»åŠŸèƒ½
                    if (response.data.state == '1') {
                        this.$store.state.visitInfo = response.data.data;
                        if (response.data.data.reviewType == '2') {
                            this.$router.push({path: '/Pass'});
                            Toast('è®¿é—®é€šè¿‡')
                        } else {
                            this.$router.push({path: '/submit'});
                            Toast('æäº¤æˆåŠŸ')
                        }
                    } else {
                        Toast(response.data.retMsg)
                    }
                }).catch(function (error) {
                    Toast(error);
                    this.isDisable = false;//é‡Šæ”¾æŒ‰é’®çš„ç‚¹å‡»åŠŸèƒ½
                });
            },
            setClass: function (index) {
                this.activeClass = index
            },
            chooseCom() {
                this.$router.push({path: '/SearchCompany'})
            }
            ,
            // å”¤èµ·é”®ç›˜
            getItem1: function (index, e) {
                this.hideshow = false;
                if (index == 1010) {
                    e.target.setAttribute('v-model', this.chepai);
                    this.chepai = this.first + this.numArr.join().replace(/,/g, '');
                    if (!this.first) {
                        this.show_chinese = true;
                    } else {
                        this.show_chinese = false;
                        this.show_allBoard = true;
                    }
                } else {
                    this.show_chinese = false;
                    this.show_allBoard = false;
                }
            },

            // é€‰æ‹©è½¦ç‰Œå·å‰é¢çš„æ±‰å­—
            checkChinese(index) {
                // å¦‚æžœç‚¹å‡»åˆ é™¤é”®ï¼Œåˆ é™¤ç¬¬ä¸€ä¸ªæ ¼çš„å†…å®¹
                if (this.ChineseList[index].id == 99) {
                    this.first = '';
                } else {
                    // æŠŠé€‰ä¸­çš„å­—èµ‹å€¼ç»™ç¬¬ä¸€ä¸ªæ ¼ï¼Œå¹¶ä¸”åˆ‡æ¢é”®ç›˜
                    this.first = this.ChineseList[index].name;
                    this.show_chinese = false;
                    this.show_allBoard = true;
                }
                /**è½¦ç‰Œå·èµ‹å€¼*/
                let val = this.first + this.numArr.join().replace(/,/g, '');
                document.getElementById('1010').value = val;
                let arr = this.msg.visitorRegisterRequiredTemplate;
                for (let i in arr) {
                    if (arr[i].code == '1010') {
                        this.msg.visitorRegisterRequiredTemplate[i].value = val;
                        break;
                    }
                }
            },
            // é€‰æ‹©è½¦ç‰Œå·åŽé¢çš„æ•°å­—å’Œå­—æ¯
            checkEnglish_num(index) {
                // å¦‚æžœç‚¹å‡»åˆ é™¤é”®ï¼Œåˆ é™¤ numArr çš„æœ€åŽä¸€ä¸ªå€¼
                if (this.English_Number[index].id == 99) {
                    this.numArr.pop();
                    // å¦‚æžœ numArr é‡Œé¢è¢«åˆ çš„æ²¡æœ‰å€¼äº†ï¼Œåˆ‡æ¢é”®ç›˜
                    if (this.numArr.length == 0) {
                        this.show_chinese = true;
                        this.show_allBoard = false;
                    }
                } else {
                    // æŠŠé€‰ä¸­çš„å€¼ push åˆ° numArr å†…
                    this.numArr.push(this.English_Number[index].name);
                    // å¦‚æžœ numArr ä¸­çš„å€¼è¶…è¿‡ 7 ä¸ªï¼ˆè½¦ç‰Œå·çš„æœ€å¤§ä½æ•°ï¼‰ï¼Œåˆ é™¤æœ€åŽä¸€ä¸ª
                    if (this.numArr.length > 7) {
                        this.numArr.pop()
                    }
                }
                /**è½¦ç‰Œå·èµ‹å€¼*/
                let val = this.first + this.numArr.join().replace(/,/g, '');
                document.getElementById('1010').value = val;
                let arr = this.msg.visitorRegisterRequiredTemplate;
                for (let i in arr) {
                    if (arr[i].code == '1010') {
                        this.msg.visitorRegisterRequiredTemplate[i].value = val;
                        break;
                    }
                }
            },
            // å…³é—­è™šæ‹Ÿé”®ç›˜
            close_keyboard() {
                this.show_chinese = false;
                this.show_allBoard = false;
            }
        },
        components: {
            Preload,
            swiper
        }
    }
</script>


