                // 'link',  // æ’å…¥é“¾æŽ¥
                // 'list',  // åˆ—è¡¨
                'justify',  // å¯¹é½æ–¹å¼
                // 'quote',  // å¼•ç”¨
                // 'emoticon',  // è¡¨æƒ…
                // 'image',  // æ’å…¥å›¾ç‰‡
                'table',  // è¡¨æ ¼
                // 'code',  // æ’å…¥ä»£ç 
                // 'undo',  // æ’¤é”€
                // 'redo'  // é‡å¤
            ]
            // ä¸‹é¢æ˜¯æœ€é‡è¦çš„çš„æ–¹æ³•
            this.editor.customConfig.uploadImgHooks = {
