                    // xhr æ˜¯ XMLHttpRequst å¯¹è±¡ï¼Œeditor æ˜¯ç¼–è¾‘å™¨å¯¹è±¡ï¼Œresult æ˜¯æœåŠ¡å™¨ç«¯è¿”å›žçš„ç»“æžœ
                    this.imgUrl = Object.values(result.data).toString()
