                    let url = Object.values(result.data)      // result.dataå°±æ˜¯æœåŠ¡å™¨è¿”å›žçš„å›¾ç‰‡åå­—å’Œé“¾æŽ¥
                    JSON.stringify(url)    // åœ¨è¿™é‡Œè½¬æˆJSONæ ¼å¼
                    insertImg(url)
                    // result å¿…é¡»æ˜¯ä¸€ä¸ª JSON æ ¼å¼å­—ç¬¦ä¸²ï¼ï¼ï¼å¦åˆ™æŠ¥é”™
                }
            }

            this.editor.create()     // åˆ›å»ºå¯Œæ–‡æœ¬å®žä¾‹
            if (!this.content) {
